import { AccountBalance, AccountCircle, ConnectWithoutContact, Help, Home, VpnKey, Logout, Notifications } from '@mui/icons-material'
import { Button } from '@mui/material'
import React from 'react'
import { Link } from 'react-router-dom'
import "./Settings.css"
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux'
import { useHistory } from 'react-router';

import { logoutAction } from "../../Redux/userSlice"




function Settings() {
    const user = (useSelector((state) => state.user.user))
    let data = user
    const dispatch = useDispatch()
    const history = useHistory()



    const handleLogout = (e) => {
        dispatch(logoutAction())
        history.push('/login')
    }
    return (
        <div className="Settings">
            <div className="card p-4">
                <div className="head fw-bold">
                    Settings
                </div>
                <div className="subhead">
                    Genaral
                </div>
                <div className="item">
                    <Link to="/AccountDetails"  style={{textDecoration:"none"}}>
                        <AccountBalance />  Account Informations
                    </Link>
                </div>

                <div className="item">
                    <Link to="/AddSocialAccount"  style={{textDecoration:"none"}}>
                        <ConnectWithoutContact /> Social Acount
                    </Link>
                </div>
                <div className="subhead">
                    Account
                </div>
                <div className="item">
                    <Link to={`/profile/${data._id}`}  style={{textDecoration:"none"}}>
                    
                    <AccountCircle />  My Account
                    </Link>
                </div>
                <div className="item">
                    <Link to='/resetPasswords' style={{textDecoration:"none"}}>
                        <VpnKey /> Password
                    </Link>
                </div>
                <div className="subhead">
                    Others
                </div>
                <div className="item">
                    <Link to='/notification' style={{textDecoration:"none"}}>
                        <Notifications />  Notification
                    </Link>
                </div>

                <div className="item" onClick ={handleLogout}  style={{color:"#007fff"}}>
                    <Logout /> Logout
                </div>

            </div>

        </div>
    )
}

export default Settings
