import React from 'react'
import './Addpost.css'
import { InputBase, Button } from "@mui/material"
import IosShareRoundedIcon from '@mui/icons-material/IosShareRounded';
import { useState } from 'react';
import { useHistory } from 'react-router-dom';
import userAvatar from '../../../Assets/userAvathar.jpg'




function Addpost({user}) {
    const [post,setPost]=useState("")
    const history =useHistory()
    let ProfilePhotos=user?.ProfilePhotos;


    const handleClick=()=>{
        history.push('/createPost')
    }
  
    
    return (
        <div className="Addpost" onClick={handleClick}>
            <div className='d-flex w-100'>

            <img src={ProfilePhotos ? ProfilePhotos[ProfilePhotos.length -1]:userAvatar} alt="" />

         

                <InputBase
                    sx={{ ml: 1, flex: 1 }}
                    fullWidth
                    style={{width:'100%'}}
                    placeholder={'What’s New, ' +user?.name +"?"  }
                    inputProps={{ 'aria-label': 'What’s New, Abhinav? ' }}
                    onChange={(e)=>setPost(e.target.value)}
                />
            </div>
            <Button variant="contained" endIcon={<IosShareRoundedIcon />}>
                POST
            </Button>
        </div>
    )
}

export default Addpost
